.news .main-content__content h1 {
  margin-bottom: 20px;
}

.main-content h1 {
  margin-top: 64px;
  margin-bottom: 42px;
}

.main-content .main-content__links {
  display: flex;
}

.main-content .main-content__links a {
  margin: auto;
}

@media only screen and (min-width: 768px) {
  .main-content {
    padding-bottom: 105px;
  }
}

@media only screen and (max-width: 767px) {
  .main-content .main-content__links {
    margin: 30px 0;
  }

  .main-content .main-content__links a {
    width: calc(320px - 36px);
    margin: auto;
    text-align: center;
    justify-content: center;
  }
}